import socketClient from './socketClient';
let localStream;

let clientPeer;
let hostPeer;

const iceServers = {
    "iceServers": [
        {
            urls: [
                'stun:ss-turn1.xirsys.com',
                'turn:ss-turn1.xirsys.com:80?transport=udp',
                'turn:ss-turn1.xirsys.com:3478?transport=udp',
                'turn:ss-turn1.xirsys.com:80?transport=tcp',
                'turn:ss-turn1.xirsys.com:3478?transport=tcp',
                'turns:ss-turn1.xirsys.com:443?transport=tcp',
                'turns:ss-turn1.xirsys.com:5349?transport=tcp'
            ],
            credential: '9d61b156-f159-11e8-807d-32b0c04e5b2c',
            username: '9d61b0d4-f159-11e8-96d8-adf817ff6a2f'
        }
    ]
};
const offerOptions = {
    offerToReceiveAudio: 1,
    offerToReceiveVideo: 1
  };
function getStream() {
    var mediaDevices = navigator.mediaDevices.getUserMedia({video: true, audio: true});
    return mediaDevices;
}
    
function playStream(stream, isHost = false) {
    try {
        let video = document.getElementById("myVideo");
        if (isHost) {
            video.muted = true;
            video.controls = false;
        }
        else {
            video.controls = true;
            video.muted = false;
        }
        video.srcObject = stream;
    } catch(ex) {
        if(localStream) {
            localStream.getTracks().forEach((track) => {track.stop();})
        }
    }
}
export function stopStream(){
    if (localStream) {
        localStream.getTracks().forEach((track) => {track.stop();});
        localStream = null;
    }
}

export const hostConnect = async () => {
    localStream = await getStream();
    playStream(localStream, true);
    socketClient.onUserJoinMyRoom(null,createOffer);
}

const createOffer = async (socketId, roomID) => {
    hostPeer = new RTCPeerConnection(iceServers);
    hostPeer.addEventListener('icecandidate', (e) => {
        socketClient.emitConnection({type: 'add', 
            candidate: e.candidate, isHost: true, 
            id: socketId, roomID}, null
        )
    })
    localStream.getTracks().forEach(track => hostPeer.addTrack(track, localStream));

    const description = await hostPeer.createOffer(offerOptions);
    hostPeer.setLocalDescription(description);

    socketClient.emitConnection({type: 'offer', description,id: socketId, isHost: true}, 
        function(answer) {
            hostPeer.setRemoteDescription(answer)
        });
}

export const clientAddIceCandidate = (candidate, isHost) => {
    if (isHost) {
        console.log('client add candidate')
        clientPeer.addIceCandidate(candidate)
    } else {
        console.log('host add candidate')
        hostPeer.addIceCandidate(candidate)
    }
}

export const clientConnect = async (userID, roomID) => {
    clientPeer = new RTCPeerConnection(iceServers);
    clientPeer.addEventListener('icecandidate', (e) => {
        socketClient.emitConnection({type: 'add', 
        candidate: e.candidate, 
        isHost: false, id: userID, roomID
    }, null)
    })
    clientPeer.addEventListener('track', (e) => {
        playStream(e.streams[0]);
    })
}

export const answer = async (offerDescription, setRemoteForCreateOffer) => {
    console.log('go answer')
    clientPeer.setRemoteDescription(offerDescription);
    const description = await clientPeer.createAnswer();
    clientPeer.setLocalDescription(description);
    setRemoteForCreateOffer(description);
}